#app {
  height: 100%;
}
html,
body {
  position: relative;
  height: 100%;
}

body {
  background: #000;
  font-family:
    Helvetica Neue,
    Helvetica,
    Arial,
    sans-serif;
  font-size: 14px;
  color: #fff;
  margin: 0;
  padding: 0;
}

.swiper {
  width: 100%;
  padding-top: 50px;
  padding-bottom: 50px;
}

.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 300px;
  height: 300px;
}

.swiper-slide img {
  display: block;
  width: 100%;
}
.liste{
    color: #fff;
    text-align: center;
   color: #fff;

}
.slide-link {
  text-decoration: none;
  color: #fff;
  font-weight: bold;
} 
.titre_movie{
  color: #fff !important;
    font-size: 1.2rem;

  font-weight: bold;
  margin-bottom: 10px;
}